.custom-button {
    min-width: 165px;
    width: auto;
    height: 50px;
    letter-spacing: 0.5px;
    line-height: 50px;
    padding: 0 15px 0 15px;
    font-size: 15px;
    background-color: black;
    color: white;
    text-transform: uppercase;
    font-family: 'Open Sans Condensed';
    font-weight: bolder;
    border: none;
    cursor: pointer;
    display:flex;
    justify-content: center;
    &:hover {
      background-color: white;
      color: black;
      border: 1px solid black;
    }
    &.sign-in-google{
      background-color:#4285F4;
      color: white;
      border:none;
      &:hover{
        background-color: #357ae8;
      }
    }
    &.inverted{
      background-color: white;
      color: black;
      border: 1px solid black;
      
      &:hover{
        background-color: black;
        color: white;
      }
    }
  }
  